import axios from "axios";
export default (ruta = "") => {

  let usuario = JSON.parse(sessionStorage.getItem("usuario"))
  let id_usuario = ""
  if (usuario) {
    id_usuario = usuario.id_usuario
  }

  let rutaApi = ""
  if (ruta == "1") {
  //  rutaApi = "https://convocatorias-api.herokuapp.com/api/"
    // rutaApi  = "http://localhost/pruebaApi/public/api/"
  } else {
    //  rutaApi = "http://localhost/Plan_A/dfp_back/public/api/" 
    // rutaApi = "http://localhost/dfp_back/public/api/" 
    // rutaApi = "http://localhost/dfp_front_gitlab/public/api/"
    //  rutaApi = "http://localhost/Vue projects/dfp_back/public/api/"
    rutaApi = "https://develdfp.cloudsenactpi.net/api/"    
    // rutaApi = "/api/"
  }

  return axios.create({
    // baseURL: "https://develdfp.cloudsenactpi.net/api/",
    baseURL: rutaApi,
    // baseURL: "/api/",
    headers: {
      "Accept": "application/json",
      "Content-Type": "application/json",
      "Usuario": id_usuario
    },
  });
};
